@font-face {
  font-family: myFont;
  src: url(/static/media/BarlowCondensed-Regular.b9aa6092.ttf);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: myFont;
  color: #001e28;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

button {
  cursor: pointer;
}

.b-container {
    height: 50vh;
}

.b-row {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    height: 100%;
}

.b-col {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #0000001f;
    width: 250px;
    height: 100px;
    margin: 15px 0;
}

.b-col svg {
    font-size: 25px;
    color: #ff0000;
}

.b-col span {
    margin-left: 10px;
    font-size: 25px;
}
.c-container {
    height: 100vh;
}

.c-row {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70%;
}

.c-col {
    position: relative;
    flex: 1 1;
    margin: 0 10px;
    box-shadow: 0 0 10px #00000049;
    transition: all 1s;
}

.c-col:hover {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
}

.c-col img {
    width: 100%;
}

.category-content {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.category-content p {
    font-size: 35px;
    text-shadow: 0 0 5px #000000;
    letter-spacing: 2px;
    font-weight: bold;
    text-transform: uppercase;
    color: #ffffff;
    margin-bottom: 20px;
}

.category-content .c-btn {
    padding: 10px 15px;
    border: none;
    outline: none;
    text-transform: uppercase;
    font-weight: bold;
    background-color: #ffffff;
    letter-spacing: 1.4px;
    transition: all 0.5s;
}

.category-content .c-btn:hover {
    background-color: #ff0000;
}

.loading {
    text-transform: uppercase;
    letter-spacing: 2px;
}

.error {
    color: #ff0000;
}

@media screen and (max-width: 769px) {
    .c-row {
        flex-direction: column;
        height: auto;
    }
    
    .c-col {
        width: 280px;
        height: auto;
        flex: unset;
        margin: 20px 0;
    }
}
